import jsPDF from "jspdf";
import "jspdf-autotable";
import React from "react";

declare module "jspdf" {
  interface jsPDF {
    autoTable: (options: any) => void;
  }
}

interface PortInfo {
  port: string;
  protocol: string;
  service: string;
  status: string;
}

interface ScanResultProps {
  ip: string;
  ports: PortInfo[];
  duration: number;
  startTime?: string;
  endTime?: string;
  onClear?: () => void; 
}


const ScanResultExporter: React.FC<ScanResultProps> = ({
  ip,
  ports,
  duration,
  startTime,
  endTime,
  onClear,
}) => {
  return (
    <div className="p-4">
      <div id="scan-report" className="bg-white text-black p-6 rounded-lg">
        <h2 className="text-2xl font-bold mb-4"> Scan Report</h2>
        <div className="mb-4">
          <p>
            <strong>Host:</strong> {ip}
          </p>
          <p>
            <strong>Scan Duration:</strong> {duration} seconds
          </p>
          <p>
            <strong>Start Time:</strong> {startTime}
          </p>
          <p>
            <strong>End Time:</strong> {endTime}
          </p>
        </div>

        <h3 className="text-xl font-semibold mb-2">Open Ports</h3>
        <table className="w-full border border-gray-400 text-sm">
          <thead>
            <tr className="bg-gray-200">
              <th className="border px-2 py-1">Port</th>
              <th className="border px-2 py-1">Service</th>
              <th className="border px-2 py-1">Status</th>
            </tr>
          </thead>
          <tbody>
            {ports.map((portInfo, idx) => (
              <tr key={idx} className="text-center">
                <td className="border px-2 py-1">{portInfo.port}</td>
                <td className="border px-2 py-1">{portInfo.service}</td>
                <td className="border px-2 py-1 text-green-600">
                  {portInfo.status}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <div className="mt-4 text-sm text-gray-600 italic">
          Report generated with https://github.com/ItsNyoty/NmapUI
        </div>
      </div>

      <div className="flex gap-2 mt-6">
        <button
          onClick={() => {
            const doc = new jsPDF();

            doc.text(`Host: ${ip}`, 10, 10);
            doc.text(`Scan Duration: ${duration} seconds`, 10, 20);
            doc.text(`Start Time: ${startTime}`, 10, 30);
            doc.text(`End Time: ${endTime}`, 10, 40);

            doc.autoTable({
              startY: 50,
              head: [["Port", "Protocol", "Service", "Status"]],
              body: ports.map((port) => [
                port.port,
                port.protocol,
                port.service,
                port.status,
              ]),
            });

            doc.save("scan_report.pdf");
          }}
          className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded"
        >
          Export Scan Result to PDF
        </button>
        <button
          onClick={onClear}
          className="bg-red-500 hover:bg-red-600 text-white px-4 py-2 rounded"
        >
          Clear Scan
        </button>
      </div>
    </div>
  );
};

export default ScanResultExporter;
